@import "../scss/variables";

.fb-template-library-button-wrapper {
	button {
		background: #399CFF;
		color: #fff;

		svg {
			width: 30px;
			height: 20px;

			path: {
				fill: #fff;
			}
		}

		&:hover {
			color: #fff !important;

			svg {
				path: {
					fill: #ffffff;
				}
			}
		}
	}
}

body {
	.components-modal__frame {
		max-height: 90% !important;

		&.fb-template-library-popover {
			min-width: calc(100% - 32px);
			height: 100% !important;

			.components-modal__content {
				margin: 0;
				padding: 24px;
				background: #F7F7F7;

				.components-modal__header {
					border: none;
				}
			}
		}
	}

	.fb-template-library-store-wrap {
		font-family: "Work Sans";

		.fb-template-library-store-header {
			border-bottom: 1px solid #E1E3E8;
			padding-bottom: 42px;

			.fb-template-library-store-header-container {
				display: flex;
				align-items: center;

				.fb-template-library-logo {
					display: flex;
					align-items: center;
					gap: 15px;

					span {
						font-weight: 700;
						font-size: 30px;
					}
				}

				.fb-template-library-tabs {
					display: flex;
					align-items: center;
					justify-content: center;
					background: #fff;
					margin-left: 538px;
					padding: 14px;
					border-radius: 14px;
					gap: 20px;

					span {
						color: #555E72;
						font-size: 16px;
						font-style: normal;
						font-weight: 500;
						line-height: 22px;
					}

					.single-tab {
						display: flex;
						align-items: center;
						gap: 8px;
						cursor: pointer;

						&.active {
							padding: 12px;
							border-radius: 6px;
							background: #399CFF;

							span {
								color: #ffffff;
							}
						}
					}

					button {
						border: none;
						background: none;
						cursor: pointer;
						z-index: 10;
					}
				}
			}
		}

		.fb-template-library-store-sidebar-wrapper {
			display: flex;

			.fb-template-library-store-sidebar {
				flex: 1;
				padding: 24px 24px 0 0;
				border-right: 1px solid #E1E3E8;
				height: 100vh;

				.fb-template-library-store-sidebar-tabs {
					.fb-template-library-store-sidebar-tab {
						display: flex;
						justify-content: space-between;
						align-items: center;
						padding: 14px;
						border: none;
						border-bottom: 1px solid #E1E3E8;
						cursor: pointer;

						span {
							color: #555E72;
							font-size: 14px;
							font-style: normal;
							font-weight: 500;
							line-height: 20px;
						}

						span.fb-template-library-store-sidebar-tab-count {
							font-size: 12px;
							line-height: 16px;
							width: 24px;
							height: 24px;
							border-radius: 50px;
							background: #EBF5FF;
							display: flex;
							align-items: center;
							justify-content: center;

						}

						&:last-child {
							border-bottom: none;
						}

						&.active {
							border-radius: 8px;
							background: #EBF5FF;
							border-bottom: 1px solid transparent;

							span.fb-template-library-store-sidebar-tab-count {
								background: #ffffff;
							}

							span.fb-template-library-store-sidebar-tab-name {
								color: #399CFF;
							}
						}

						&:has(+ .active) {
							border-bottom: 1px solid transparent;
						}
					}

					button {
						border: none;
						background: none;
						width: 100%;
					}
				}
			}

			.fb-template-library-store-content {
				flex: 6;
				padding: 24px 0 0 24px;

				.fb-template-library-cards {
					display: flex;
					gap: 30px;
					flex-wrap: wrap;

					.fb-template-library-card {
						background: #fff;
						border-radius: 14px;

						.fb-template-library-card-content {
							padding: 14px;
							display: flex;
							align-items: center;
							justify-content: space-between;

							.fb-template-library-import {
								display: flex;
								align-items: center;
								padding: 8px 12px;
								gap: 8px;
								border-radius: 6px;
								background: #399CFF;
								cursor: pointer;

								span {
									color: #ffffff;
									font-size: 16px;
									font-weight: 500;
									line-height: 22px;
								}
							}

							.fb-template-library-save {
								display: flex;
								align-items: center;
								gap: 14px;

								.save-wrapper {
									display: flex;
									align-items: center;
									gap: 6px;
									cursor: pointer;

									span {
										color: #555E72;
										font-size: 14px;
										font-weight: 500;
										line-height: 20px;
									}
								}

								.favourite-wrapper {
									display: flex;
									align-items: center;
									padding: 10px;
									border-radius: 50px;
									border: 1px solid #E1E3E8;
									background: #F7F7F7;
									cursor: pointer;
								}
							}
						}
					}
				}
			}

		}
	}

}

.fb-fsi-modal-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.5);
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 160000;
	height: 100vh;

	.fb-fsi-modal {
		background: #ffffff;
		border-radius: 3px;
		width: 100vw;
		height: 100vh;
		max-height: 100vh;
		overflow-y: auto;

		.fb-fsi-modal-header {
			padding: 15px 20px;
			border-bottom: 1px solid #ddd;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.fb-fsi-modal-close {
				border: none;
				background: none;
				font-size: 24px;
				cursor: pointer;
				padding: 0 5px;
			}
		}

		.fb-fsi-modal-content-wrapper {
			display: flex;

			.fb-fsi-modal-sidebar-content {
				flex: 1;
				padding: 20px;
				position: relative;

				.fb-global-color-section {
					margin-bottom: 24px;

					.fb-color-circle {
						width: 40px;
						height: 40px;
						border-radius: 50%;
						cursor: pointer;
						transition: transform 0.2s ease;
					}

					.fb-color-item {
						.fb-color-tooltip {
							position: absolute;
							top: -30px;
							left: 50%;
							transform: translateX(-50%);
							background-color: #000;
							color: #fff;
							padding: 4px 8px;
							border-radius: 4px;
							font-size: 12px;
							white-space: nowrap;
							z-index: 1000;
							pointer-events: none;

							&:after {
								content: '';
								position: absolute;
								top: 100%;
								left: 50%;
								transform: translateX(-50%);
								border-width: 4px;
								border-style: solid;
								border-color: #000 transparent transparent transparent;
							}
						}
					}
				}

				.fb-fsi-typography-panel {
					.dashicons {
						&::before {
							font-size: 14px;
						}
					}

					.fb-fsi-typography-header {
						display: flex;
						justify-content: space-between;
						align-items: center;
						margin-bottom: 24px;

						h3 {
							margin: 0;
							font-size: 16px;
							font-weight: 600;
						}
					}

					.fb-fsi-panel-section {
						border: 1px solid #ddd;
						border-radius: 12px;
						margin-bottom: 10px;

						.fb-fsi-panel-header {
							width: 100%;
							display: flex;
							justify-content: space-between;
							align-items: center;
							padding: 12px 16px;
							background: none;
							border: none;
							border-radius: 12px;
							cursor: pointer;
							font-weight: 500;
							text-align: left;
							transition: background-color 0.2s ease;
						}

						.fb-fsi-panel-content {
							padding: 8px 16px;

							.fb-font-item {
								display: flex;
								align-items: center;
								padding: 8px 0;
								gap: 12px;

								&:last-child {
									border-bottom: none;
								}

								.fb-font-preview {
									width: 40px;
									height: 40px;
									background: #f5f5f5;
									border-radius: 4px;
									display: flex;
									align-items: center;
									justify-content: center;
									font-size: 18px;
								}

								.fb-font-details {
									display: flex;
									justify-content: space-between;
									align-items: center;
									flex-grow: 1;

									span {
										color: #1e1e1e;
										font-size: 13px;
									}

									.fb-edit-button {
										background: none;
										border: none;
										padding: 4px;
										cursor: pointer;
										color: #666;
									}
								}
							}
						}
					}

					.fb-fsi-typography-popup {
						background: #fff;
						position: absolute;
						top: 100px;
						left: 100%;
						width: 100%;
						margin-left: 20px;
						border: 1px solid #ddd;
						border-radius: 12px;
						padding: 24px;
						z-index: 99;
					}
				}
			}

			.fb-fsi-modal-main-content {
				flex: 6;
			}
			.fb-blocks-tabs {
				.fb-tab-element {
					margin-bottom: 10px;
				}

				.components-navigator-button {

					font-size: $medium-font-size-global;
					font-weight: 400;
					line-height: 16px;
					padding: 0;
				}

				.fb-tab-wrapper {
					display: flex;
					align-items: center;
					flex-direction: row;
					gap: calc(8px);
					-webkit-box-pack: start;
					justify-content: flex-start;
					width: 100%;

					.fb-tab-icon {
						svg {
							fill: none !important;
						}
					}

					.fb-tab-title {

						font-size: $medium-font-size-global;
						font-weight: 400;
						line-height: 16px;
						color: $neutral-color-700-global;
					}

					&:hover {

						.fb-tab-title,
						.fb-tab-icon path {
							color: $primary-color-base;
						}
					}
				}
			}
			.fb-global-header-inner-wrapper {
				display: flex;
				gap: 8px;
				.fb-global-header-inner {
					h3 {
						font-size: $medium-font-size-global;
						font-weight: 500;
						line-height: 16px;
						color: $neutral-color-950;
						text-transform: capitalize;
						margin-top: 9px;
					}

					p {

						font-size: $medium-font-size-global;
						font-weight: 400;
						line-height: 20px;
						color: $neutral-color-950;
					}
				}

				svg {
					fill: none !important;
					&:hover {
						path {
							stroke: $primary-color-base;
						}
					}
				}

			}

			.fb-global-font-sizes-presets {
				.font-size-presettes-wrapper {
					display: flex;
					align-items: center;
					justify-content: space-between;
				}
			}
		}

		.fb-fsi-modal-footer {
			padding: 15px 20px;
			border-top: 1px solid #ddd;
			display: flex;
			justify-content: flex-end;
			gap: 10px;
		}

		.fb-blocks-color-tabs {
			.components-tab-panel__tabs {
				padding-left: 16px;
				border-top: 1px solid rgb(221, 221, 221);
				border-left: 1px solid rgb(221, 221, 221);
				border-right: 1px solid rgb(221, 221, 221);

				button {
					font-size: $medium-font-size-global;
					font-weight: 500;
					line-height: 16px;
					border-bottom: 2px solid transparent;

					&.active-tab {
						// border-bottom: 2px solid $neutral-color-200;
					}

					&:first-child {
						padding-left: 0;
					}
				}
			}

			.components-tab-panel__tab-content {
				.components-tools-panel {
					.components-flex {
						&.components-h-stack {
							padding-top: 16px;
							padding-left: 12px;
							padding-right: 3px;

							button {
								svg {
									fill: $neutral-color-600;
								}
							}
						}
					}

					.components-tools-panel-item {
						border-left: 0;
						border-right: 0;
						margin-top: 0;

						.components-button {
							border-right: 0;
							border-left: 0;
							//padding: 14px 12px;
							padding: 0 12px;
							height: 48px;

							.components-flex-item {
								color: $neutral-color-700;
								font-family: $body-font-family;
								font-weight: 500;
								font-size: 14px;
								line-height: 16px;
							}
						}

						.components-flex {
							&.components-h-stack {
								padding-top: 0;
								padding-left: 0;
							}
						}
					}
				}
			}

			.add-custom-color {
				text-align: center;
				padding: 14px 0;

				.add-custom-color-btn {
					padding: 12px 14px;
					background: $primary-color;
					height: 44px;
					border-radius: 6px;
					font-family: $body-font-family;
					font-weight: 500;
					color: $white-color;
					font-size: 14px;
					line-height: 16px;
					align-items: center;
					gap: 10px;

					span {
						height: 100%;
						display: flex;
						align-items: center;
					}
				}
			}
		}
		.fb-global-typography-element-content {
			padding-top: 10px;

			.fb-setting-label-text{
				font-weight: 500;
				font-size: 11px;
				text-transform: uppercase !important;
				font-family: $body-font-family;
			}

			.fb-typography-settings {
				padding: 0;
				margin-top: 10px;

				.components-input-control__container {
					.components-input-control__input {
						&:focus {
							outline: none;
						}
					}

					.components-select-control__input {
						background: $neutral-color-50;
						height: unset;
						padding: 6px 12px;

						&:focus {
							outline: none;
						}
					}

					.components-input-control__backdrop {
						border: none;
					}
				}

				.components-range-control__wrapper {
					span {
						&:nth-child(2) {
							height: 2px;
							background: $neutral-color-200;
						}

						&:nth-child(3) {
							height: 2px;
							background: $primary-color-base;
						}
					}

					.components-range-control__thumb-wrapper {
						span {
							background-color: $primary-color-base;
						}
					}

				}

				.components-range-control__number {
					.components-input-control__container {
						border: 1px solid $primary-color-base;
						border-radius: 6px;
						margin-left: 10px;
					}
				}

				.fb-setting-spacing-label-container {
					margin: 14px 0;
				}

				.components-input-control__label {
					margin: 10px 0;
					font-weight: 500;
					font-family: $body-font-family;
				}
			}

			.components-base-control {
				width: auto;
				margin-left: 0;

				.fb-global-typography-headings-wrap {
					margin: 0 auto;
					margin-top: 12px;
				}
			}

			&:empty {
				display: none;
			}
		}
		.font_preview_box {
			align-items: center;
			background: $neutral-color-100;
			border-radius: 2px;
			display: flex;
			justify-content: center;
			margin-bottom: 16px;
			min-height: 100px;
			overflow: hidden;
		}
	}
}

.fb-fsi-modal-main-content {
	position: relative;
	height: 600px;
	background: #f0f0f1;
	border: 1px solid #ddd;
	border-radius: 4px;
	overflow: hidden;
}

.fb-site-preview-container {
	position: relative;
	width: 100%;
	height: 100%;
}

.fb-preview-frame {
	width: 100%;
	height: 100%;
	border: none;
}

.fb-preview-loader {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 10px;
}

.fb-loader-spinner {
	width: 40px;
	height: 40px;
	border: 3px solid #f3f3f3;
	border-top: 3px solid #646970;
	border-radius: 50%;
	animation: spin 1s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}